    <div>
        <loader :is-visible="isLoading"></loader>
        <section class="page-action">

            <div class="container-fluid p-0">
                <div class="row">
                    <div class="col-md-12 padding-t_8">

                        <div class="theme-primary partition p-actions-expand tablehead">
                            <span class="p-name text-white">{{ $t('UserManual') }}</span>
                            <router-link to="/Help/HelpAndGuide"><span class="p-actions"><a title=""
                                        class="p-action-btn text-white float-right" data-toggle-tooltip="tooltip"
                                        data-original-title="Back">
                                        <em class="fa fa-angle-double-left"></em><br />{{ $t("Back") }}</a>

                                </span> </router-link>
                            <span @click="DownloadZipFile(UrlList)" class="p-actions float-right"><a title=""
                                    class="p-action-btn text-white float-right" data-toggle-tooltip="tooltip"
                                    data-original-title="Download">
                                    <em class="fa fa-arrow-down"></em><br />{{ $t("Download") }}</a>

                            </span>

                        </div>
                    </div>
                </div>

            </div>

            <div class="row" style="margin-top:10px;">

               
                <div class="col-md-4 mb-3" v-for="item in UserManualList" :key="item">
                    
                    <div class="border py-4 px-3">
                        <div class="row d-flex align-items-center">
                            <div class="col-md-2">
                                <div class="text-center font-44 geraldine" v-html="item.icon">                 
                                </div>
                                
                            </div>

                            <div class="col-md-7" id="Userlist">
                                <h5 class="card-title font-18">{{ item.ArticleCategoryName }}</h5>
                                <p class="card-text font-14">{{item.ManualDescription}}</p>
                            </div>
                            <div class="col-md-3">
                                <div class="text-center equator-color">
                                    <!-- <a :href="item.fileUrl" target="_blank">
                                        <em class="fa fa-eye font-weight-light text-info font-28 mr-2"
