                                    </a> -->

                                    <a href="#"><em class="fa fa-download font-weight-light text-primary font-28" aria-hidden="true"
                                        @click="DownloadSingleFile(item.FileUrl,item.Articlename)"></em></a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>


            </div>
        </section>
    </div>
</template>

<script>
import DataService from '../../services/DataService';

import { storeToRefs } from "pinia";
import { useloggedUserInfo } from "@/stores/UserInfo";
const { GetUserInfo } = storeToRefs(useloggedUserInfo());
export default
    {
        name: 'UserManual',
        data() {
            return {

                UserManualList: [],                
                UrlList: [],
                isLoading: false,
                UserManualDescription: '',
                UserManualIcons: '',
                
            }
        },
        created: function () {
            this.BindUserManualList()
        },
        methods: {
            BindUserManualList: function () {
              
                var vm = this;
                vm.isLoading = true;

                var params = `search=&userType=${GetUserInfo.value.UserType}&articleType=UserManual&moduleId=`
                
                DataService.GetArticleList(params).then(function (response) {
                    
                    if (response.data != null) {

                        response.data.forEach((value, index) => {
                               if(value.articleCatagoryName == 'Company Admin')
                               {
                                  vm.UserManualDescription = 'Lorem Ipsum Dolor';
                                  vm.UserManualIcons = '<em class="fa fa-ticket" aria-hidden="true"></em>';
                               }
                               else if(value.articleCatagoryName == 'Company Client Help')
                               {
                                  vm.UserManualDescription = 'Lorem Ipsum Dolor';
                                  vm.UserManualIcons = '<em class="fa fa-address-book" aria-hidden="true"></em>';
                               }
                               else if(value.articleCatagoryName == 'PROJECT MANAGER')
                               {
                                  vm.UserManualDescription = 'Lorem Ipsum Dolor';
                                  vm.UserManualIcons = '<em class="fa fa-bandcamp" aria-hidden="true"></em>';
                               }
                            let myobj = {
                                Articlename: value.articleName,
                                ArticleCategoryName: value.articleCatagoryName,                                
                                ArticleId: value.articleId,
                                FileUrl: value.fileUrl,
                                href: $(value.articleName).attr("href"),
                                ManualDescription: vm.UserManualDescription,
                                icon: vm.UserManualIcons,
                            }
                            vm.UserManualList.push(myobj);
                            vm.isLoading = false;
                            
                        })

                        response.data.forEach((value, index) => {
                            
                            if (value.fileUrl != null) {
                                vm.UrlList.push(value.fileUrl)
                            }

                        })
                        
                    }
                    vm.isLoading = false;
                });
            },
            // downloadAll: function () {
            //       
            //     var cm = this;

            //     $('#Userlist').find('a').each(function () {

            //         cm.UrlList.push($(this).attr('href'));
